<template>
    <label for="add-select-brand" class="block text-sm font-medium text-black mb-1"> *</label>
    <div class="relative inline-block w-full max-w-150">
        <!--     -->
        <div v-if="!isMobile" class="hidden md:block relative">
            <div class="relative w-full">
                <Select 
                    v-model="selectedBrand" 
                    inputId="add-select-brand" 
                    :options="filteredBrands" 
                    optionLabel="name" 
                    optionValue="id" 
                    placeholder="" 
                    :allowClear="true"
                    class="relative w-full h-10 border! border-neutral-200! rounded-sm! bg-white! hover:border-bamper-200! focus:border-bamper-500! focus:outline-none transition-all duration-200 cursor-pointer"
                    :class="{ 'border-red-500': invalid }" 
                    :pt="desktopPtOptions"
                    :invalid="invalid"
                    showClear
                    @before-show="onShow"
                    @before-hide="onHide"
                    @clear="clearSelection"
                    :filter="false"
                    :appendTo="appendToRef"
                    @update:modelValue="onSelectChange"
                >
                    <!--     -->
                    <template #option="slotProps">
                        <div class="w-full flex items-center gap-x-2 hover:bg-bamper-50 border-none px-3 py-2.5 transition-all duration-200"
                        >
                            <!--  slotProps.selected    selectedBrand -->
                            <svg v-if="slotProps.selected" class="w-5 h-5 text-bamper-500 hover:text-bamper-600 shrink-0" fill="currentColor" viewBox="0 0 20 20">
                                <path fill-rule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clip-rule="evenodd" />
                            </svg>
                            <span 
                                :class="{
                                    'truncate text-sm font-regular':'',
                                    'text-bamper-500 hover:text-bamper-600': slotProps.selected,
                                    'text-black': !slotProps.selected,
                                }"
                                >
                                {{ slotProps.option.name }}
                            </span>
                        </div>
                    </template>
                    <!--    -->
                    <template #dropdownicon>
                        <button type="button" class="flex w-8 h-8 items-center justify-center border-none mr-1 text-neutral-400 cursor-pointer" >
                            <svg class="block w-4 h-4 stroke-current shrink-0 transition-transform duration-100" 
                            :class="{ 'rotate-180': overlayVisible }" fill="none" viewBox="0 0 16 16">
                                <path d="M4 6L8 10L12 6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                            </svg>
                        </button>
                    </template>
                    <!--     div -->
                    <template #clearicon="{ clearCallback }">
                        <div class="custom-clear-wrapper absolute right-1 top-1/2 transform -translate-y-1/2 bg-white z-10">
                            <button 
                                type="button" 
                                @click="clearCallback"
                                class=" flex items-center justify-center border-none w-8 h-8 p-1 text-neutral-400 hover:text-neutral-600 cursor-pointer"
                            >
                                <svg class="block w-5 h-5 shrink-0 stroke-current" fill="none" viewBox="0 0 20 20">
                                    <path d="M14.1667 5.83333L5.83337 14.1667M5.83337 5.83333L14.1667 14.1667" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                                </svg>
                            </button>
                        </div>
                    </template>
                    <!--     -->
                    <template #value="slotProps">
                        <div class="w-full flex items-center justify-between px-3 py-2.5 h-10">
                            <span v-if="slotProps.value" class="text-sm text-black truncate">
                                {{ getSelectedBrandName(slotProps.value) }}
                            </span>
                            <span v-else class="text-sm text-neutral-400">
                                <!--  -->
                            </span>
                        </div>
                    </template>
                    <!--     -->
                    <template #header>
                        <div class="w-full pb-1 bg-white sticky top-0 z-10">
                            <div class="relative">
                                <input 
                                    v-model="desktopSearch"
                                    type="text"
                                    placeholder=""
                                    class=" w-full px-10 py-2.5 h-10 text-sm border border-neutral-200 rounded-sm focus:outline-none focus:border-bamper-500"
                                    @input="onSearchInput"
                                    ref="desktopSearchInput"
                                />
                                <!--    -->
                                <svg class="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-neutral-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                                </svg>
                                <!--     -->
                                <button 
                                    v-if="desktopSearch"
                                    @click="clearDesktopSearch"
                                    class="absolute right-3 top-1/2 transform -translate-y-1/2 text-neutral-400 hover:text-neutral-600 cursor-pointer"
                                    type="button"
                                >
                                    <svg class="block w-5 h-5 shrink-0 stroke-current" fill="none" viewBox="0 0 20 20">
                                        <path d="M14.1667 5.83333L5.83337 14.1667M5.83337 5.83333L14.1667 14.1667" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"></path>
                                    </svg>
                                </button>
                            </div>
                        </div>
                    </template>
                    <!--      -->
                    <template #empty>
                        <div class="px-3 py-3 text-center">
                            <div v-if="desktopSearch" class="text-sm text-neutral-400">
                                 
                            </div>
                        </div>
                    </template>
                </Select>
                <div ref="overlayContainer" class="select-overlay-container"></div>
            </div>
        </div>
        <!--   -->
        <div v-else class="w-full md:hidden">
            <button 
                @click="openMobileModal"
                class="w-full max-w-full px-3 py-2.5 text-sm font-regular text-black border border-neutral-200 rounded-sm bg-white hover:border-bamper-200 focus:border-bamper-500 focus:outline-none transition-all duration-200 cursor-pointer flex items-center justify-between"
                :class="{ 'border-red-500': invalid }"
            >
                <span v-if="selectedBrand" class="text-sm text-black truncate">
                    {{ getSelectedBrandName(selectedBrand) }}
                </span>
                <span v-else class="text-sm text-neutral-400">
                    <!--  -->
                </span>
                
                <div class="relative flex items-center gap-2">
                    <button 
                        v-if="selectedBrand" 
                        @click.stop="clearSelection"
                        class="absolute -right-1 top-1/2 transform -translate-y-1/2 bg-white p-1 border-none text-neutral-400 hover:text-neutral-600 cursor-pointer transition-colors duration-200"
                        type="button"
                    >
                        <svg class="w-5 h-5 stroke-current" fill="none" viewBox="0 0 20 20">
                            <path d="M14.1667 5.83333L5.83337 14.1667M5.83337 5.83333L14.1667 14.1667" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                        </svg>
                    </button>
                    <svg class="w-5 h-5 transition-transform duration-200 stroke-neutral-400" fill="none" viewBox="0 0 20 20">
                        <path d="M15 12.5L10 7.5L5 12.5" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                    </svg>
                </div>
            </button>
        </div>
        <!--      -->
        <div 
            v-if="mobileModalVisible && isMobile"
            class="fixed inset-0 z-50 flex items-end justify-center bg-black/30 bg-opacity-50 md:hidden"
            @click.self="closeMobileModal"
        >
            <div class="w-full max-w-lg mt-8 p-4 h-[calc(100%-32px)] flex flex-col bg-white rounded-t-xl shadow-[0_2px_2px_0_rgba(0,0,0,0.05)] animate-slide-up">
                <div class="sticky top-0 bg-white pb-3 flex items-center justify-between">
                    <h3 class="text-lg font-medium text-black"> </h3>
                    <button 
                        @click="closeMobileModal"
                        class=" flex items-center justify-center w-6 h-6 text-neutral-400 hover:text-neutral-600 p-1 cursor-pointer rounded-sm border-none"
                    >
                        <svg class="block w-3 h-3 stroke-black shrink-0" fill="none" viewBox="0 0 12 12">
                            <path d="M10.75 0.75L0.75 10.75M0.75 0.75L10.75 10.75" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                        </svg>
                    </button>
                </div>
                <div class="py-2">
                    <div class="relative">
                        <svg class="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-neutral-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                        </svg>
                        <input 
                            v-model="mobileSearch"
                            type="text"
                            placeholder=""
                            class="w-full py-2.5 px-10 h-10 text-sm border border-neutral-200 rounded-lg focus:outline-none focus:border-bamper-500 pr-10"
                            ref="mobileSearchInput"
                        />
                            
                        <button 
                            v-if="mobileSearch"
                            @click="clearMobileSearch"
                            class="absolute flex items-center justify-center w-6 h-6 right-2 top-1/2 transform -translate-y-1/2 text-neutral-400"
                            >
                            <svg class="block w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 16 16">
                                <path d="M11.3333 4.66669L4.66663 11.3334M4.66663 4.66669L11.3333 11.3334" stroke="#A1A1A1" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                            </svg>
                        </button>
                    </div>
                </div>
                <div class="h-full overflow-y-auto">
                    <div class="max-h-screen overflow-y-auto modal-scroll">
                        <div 
                            v-for="brand in mobileFilteredBrands"
                            :key="brand.id"
                            @click="selectBrandMobile(brand)"
                            class="px-0 py-2.5 flex items-center gap-x-2 cursor-pointer transition-colors duration-250"
                            :class="{
                                'text-bamper-500': selectedBrand === brand.id,
                            }"
                        >
                            <svg 
                                :class="{
                                    'text-bamper-500': selectedBrand === brand.id,
                                    'text-transparent': selectedBrand !== brand.id,
                                }"
                                class="w-5 h-5 shrink-0" 
                                stroke="currentColor" 
                                fill="none"
                                viewBox="0 0 20 20">
                                <path d="M16.6668 5L7.50016 14.1667L3.3335 10" stroke-width="1.67" stroke-linecap="round" stroke-linejoin="round" />
                            </svg>
                            <span 
                                :class="{
                                    'text-sm font-regular': '',
                                    'text-bamper-500': selectedBrand === brand.id,
                                    'text-black': selectedBrand !== brand.id,
                                }">
                                {{ brand.name }}
                            </span>
                        </div>
                        <div v-if="mobileFilteredBrands.length === 0" class="p-4 text-center">
                            <div class="text-neutral-400 mb-0"> </div>
                            <div v-if="mobileSearch" class="text-xs text-neutral-500">
                                <!--    -->
                            </div>
                        </div>
                    </div>
                </div>
                <div class="sticky mt-auto pt-4 bottom-0">
                    <button 
                        @click="closeMobileModal"
                        class="w-full flex items-center justify-center h-10 gap-x-2 text-sm font-medium py-2 px-3 text-center text-white border border-solid border-bamper-500 bg-bamper-500 rounded-lg cursor-pointer hover:text-white hover:border-bamper-600 hover:bg-bamper-600 focus:outline-2 focus:outline-offset-1 focus:outline-bamper-500 focus:bg-bamper-500 active:text-white active:border-bamper-700 active:bg-bamper-700 disabled:text-neutral-400 disabled:border-neutral-200 disabled:bg-neutral-200 disabled:cursor-not-allowed transition-all duration-250 text-nowrap"
                    >
                        
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted, nextTick } from 'vue';
import Select from 'primevue/select';

const props = defineProps({
    invalid: {
        type: Boolean,
        default: false
    }
});

const selectedBrand = ref(null);
const overlayVisible = ref(false);
const isMobile = ref(false);
const mobileModalVisible = ref(false);
const mobileSearch = ref('');
const desktopSearch = ref('');
const mobileSearchInput = ref(null);
const desktopSearchInput = ref(null);
const overlayContainer = ref(null);

const brands = ref([
    { id: 1, name: 'Toyota' },
    { id: 2, name: 'BMW' },
    { id: 3, name: 'Audi' },
    { id: 4, name: 'Mazda' },
    { id: 5, name: 'Ford' },
    { id: 6, name: 'Mercedes-Benz' },
    { id: 7, name: 'Honda' },
    { id: 8, name: 'Nissan' },
    { id: 9, name: 'Volkswagen' },
    { id: 10, name: 'Hyundai' }
]);

const appendToRef = computed(() => {
    return overlayContainer.value || 'body';
});

//  
const filteredBrands = computed(() => {
    if (!desktopSearch.value) return brands.value;
    
    return brands.value.filter(brand =>
        brand.name.toLowerCase().includes(desktopSearch.value.toLowerCase())
    );
});

//  
const mobileFilteredBrands = computed(() => {
    if (!mobileSearch.value) return brands.value;
    
    return brands.value.filter(brand =>
        brand.name.toLowerCase().includes(mobileSearch.value.toLowerCase())
    );
});

const handleDesktopSearch = (event) => {
    desktopSearch.value = event.target.value;
    //     computed 
};

//    
const clearDesktopSearch = () => {
    desktopSearch.value = '';
    if (desktopSearchInput.value) {
        desktopSearchInput.value.value = '';
        desktopSearchInput.value.dispatchEvent(new Event('input', { bubbles: true }));
        desktopSearchInput.value.focus();
    }
};

// PT   Select
const desktopPtOptions = {
    root: ({ props }) => ({
        class: [
            'relative inline-flex cursor-pointer select-none',
            'w-full rounded-sm border border-neutral-200 bg-white',
            'transition-colors duration-200',
            'hover:border-bamper-200 focus-within:border-bamper-500 focus-within:outline-none',
            { 
                'border-red-500': props.invalid,
                '': props.modelValue
            }
        ]
    }),
    input: {
        class: [
            'w-full px-3 py-2.5 text-sm font-regular text-black',
            'bg-transparent border-0 outline-none',
            'placeholder:text-neutral-400',
            'cursor-pointer',
            'pr-8'
        ]
    },
    trigger: ({ props }) => ({
        class: [
            'flex items-center justify-center shrink-0',
            'absolute right-3 top-1/2 transform -translate-y-1/2',
            'text-neutral-400 hover:text-neutral-600',
            'transition-colors duration-200',
            { 'right-8': props.modelValue }
        ]
    }),
    //   clearIcon  PT ,     
    clearIcon: {
        class: ['hidden'] //   
    },
    filter: {
        class: ['hidden'] //   
    },
    overlay: {
        class: [
            'absolute top-full left-0 right-0 mt-1 p-2',
            'bg-white! border! border-neutral-200! rounded-sm! shadow-md!',
            'z-50 h-66 overflow-hidden',
            'min-w-full'
        ]
    },
    header: {
        class: 'p-0 border-0'
    },
    wrapper: {
        class: 'py-1'
    },
    listContainer: {
        class: 'h-50 overflow-y-auto'
    },
    list: {
        class: 'list-none m-0 p-0'
    },
    option: ({ context }) => ({
        class: [
            'relative flex items-center justify-between',
            'px-3 py-2.5 text-sm font-regular text-black',
            'cursor-pointer select-none',
            'transition-colors duration-150',
            'hover:text-bamper-600 hover:bg-bamper-50',
            {
                'text-bamper-600 bg-bamper-50 font-medium': context.selected,
                'text-neutral-400 cursor-default': context.disabled
            }
        ]
    }),
    emptyMessage: {
        class: 'px-3 py-2.5 text-sm text-neutral-400'
    }
};

const getSelectedBrandName = (value) => {
    const brand = brands.value.find(b => b.id === value);
    return brand ? brand.name : '';
};

const clearSelection = (event) => {
    if (event) event.stopPropagation();
    selectedBrand.value = null;
    desktopSearch.value = '';
};

const onShow = () => {
    overlayVisible.value = true;
    //      
    // setTimeout(() => {
    //     if (desktopSearchInput.value) {
    //         desktopSearchInput.value.focus();
    //     }
    // }, 100);
};

const onHide = () => {
    overlayVisible.value = false;
    desktopSearch.value = '';
};

const onFilter = (event) => {
    desktopSearch.value = event.value;
};

const onSearchInput = (event) => {
    desktopSearch.value = event.target.value;
    //   PrimeVue
    const filterEvent = { value: event.target.value };
    onFilter(filterEvent);
};



//     
const openMobileModal = async () => {
    mobileModalVisible.value = true;
    mobileSearch.value = '';
    document.body.style.overflow = 'hidden';
    
    await nextTick();
    if (mobileSearchInput.value) {
        setTimeout(() => {
            mobileSearchInput.value.focus();
        }, 300);
    }
};

//   
const closeMobileModal = () => {
    mobileModalVisible.value = false;
    mobileSearch.value = '';
    document.body.style.overflow = '';
};

//    
const clearMobileSearch = () => {
    mobileSearch.value = '';
    if (mobileSearchInput.value) {
        mobileSearchInput.value.focus();
    }
};

//    
const selectBrandMobile = (brand) => {
    selectedBrand.value = brand.id;
    setTimeout(() => {
        closeMobileModal();
    }, 300);
};

//   
const checkMobile = () => {
    isMobile.value = window.innerWidth < 768;
};

// Lifecycle hooks
onMounted(() => {
    checkMobile();
    window.addEventListener('resize', checkMobile);
});

onUnmounted(() => {
    window.removeEventListener('resize', checkMobile);
    document.body.style.overflow = '';
});
</script>

<style scoped>
    /*   overlay */
.select-overlay-container {
    position: relative;
    width: 100%;
}

/*   overlay */
:deep(.p-select-overlay) {
    position: absolute !important;
    top: 100% !important;
    left: 0 !important;
    width: 100% !important;
    min-width: 100% !important;
    max-width: 100% !important;
}
/*       */
:deep(.p-select-overlay .p-select-items-wrapper::-webkit-scrollbar) {
    width: 6px;
}

:deep(.p-select-overlay .p-select-items-wrapper::-webkit-scrollbar-track) {
    background: #f1f5f9;
    border-radius: 3px;
}

:deep(.p-select-overlay .p-select-items-wrapper::-webkit-scrollbar-thumb) {
    background: #cbd5e1;
    border-radius: 3px;
}

:deep(.p-select-overlay .p-select-items-wrapper::-webkit-scrollbar-thumb:hover) {
    background: #94a3b8;
}

/*     */
.modal-scroll {
    scrollbar-width: thin;
    scrollbar-color: #cbd5e1 #f1f5f9;
}

.modal-scroll::-webkit-scrollbar {
    width: 4px;
}

.modal-scroll::-webkit-scrollbar-track {
    background: #f1f5f9;
}

.modal-scroll::-webkit-scrollbar-thumb {
    background: #cbd5e1;
    border-radius: 2px;
}

/*      */
@keyframes slide-up {
    from {
        transform: translateY(100%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.animate-slide-up {
    animation: slide-up 0.3s ease-out;
}
</style>